import Vue from 'vue';
import VueResource from 'vue-resource';

Vue.use(VueResource);

let session = window.sessionStorage;
let log = (console && console.log) || function() {}; // eslint-disable-line
let env = process.env;

Vue.http.interceptors.push(function(request, next) {
  if (request.method.toLowerCase() === 'get') {
    var cache = session.getItem(`CACHE_${request.url}`);

    if (cache) {
      log('cache hit', request.url);
      next(request.respondWith(cache, { status: 200, statusText: 'Ok' }));
    } else {
      log('cache miss', request.url);
    }
  }

  next(function(response) {
    let { status, statusText, body } = response;
    if (status === 200 && request.method.toLowerCase() === 'get') {
      log('cache save', request.url);
      session.setItem(`CACHE_${request.url}`, body);
    }

    setTimeout(
      request.respondWith(body, { status, statusText }),
      env.NODE_ENV === 'development' ? 5000 : 0
    );
  });
});
