<template>
  <div class="events">
    <template v-for="(event, index) in getEvents()" >
      <p v-bind:key="index">
        <img class="event" v-bind:src="eventImgSrc(event.type_of_event)" />{{getExtraEventInfo(event)}} {{event.player}} ({{event.time}})
      </p>
      </template>

  </div>
</template>

<script>
export default {
  name: 'Events',
  props: {
    events: {},
    full: {},
    expanded: {}
  },
  data: function() {
    return {
      expandText: '+'
    };
  },
  methods: {
    getEvents: function() {
      let fullEvents = this.full;
      let filter = fullEvents ? RegExp('.*') : RegExp('goal|red');

      let listOfEvents = this.events.filter(event =>
        filter.test(event.type_of_event)
      );

      return listOfEvents;
    },
    eventImgSrc: function(event) {
      return require('../assets/events/' + event.toLowerCase() + '.png');
    },
    getExtraEventInfo: function(event) {
      let extra = '';
      switch (event.type_of_event) {
        case 'goal-penalty':
          extra = '(P)';
          break;
        case 'goal-own':
          extra = '(OG)';
          break;
      }

      return extra;
    }
  }
};
</script>

